.audio-section{
    display: flex;
    flex-direction: column;
    padding: 0% 10%;
    padding-bottom: 5%;
}

.audio-section hr{
    width: 100%;
    height: 0.1rem;
    border: none;
    background-color: #e5e7eb;
}

.audio-intro{
    display: flex;
    justify-content: space-between;
}

.audio-intro p{
    color: rgb(101, 117, 139);
}

.filters{
    display: flex;
    align-items: center;
    gap: 30px;
}

.filters-tag{
    color: #374151;
    padding: 0.25rem 0.75rem;
    border-radius: 9999px;
    border: 1px solid;
    border-color:#cfccccd1;
    font-size: 0.875rem;
    font-weight: 700;
    height: 20px;
}

.waveforms-div{
    display: flex;
    flex-direction: column;
    gap: 30px;
}
